import React from 'react'
import { useGameStateContext } from '../../contexts/GameStateContext'
import { Button } from '../../elements/button'
import { FlexContainer } from '../../elements/flex'
import { Theme } from '../../theme'
import { Icon } from '../Icon'
import ChestIcon from '../../icons/svg/lorc/locked-chest.svg'

export const Chest = () => {
  const { openCurrent, currentEncounter } = useGameStateContext()

  return (
    <FlexContainer
      $direction='column'
      style={{
        margin: '20px 20px 20px 0',
        padding: 16,
        background: Theme.darkBgColor,
        alignItems: 'center',
      }}
    >
      <h1
        style={{
          marginTop: 0,
          color: 'white',
          fontFamily: Theme.titleFont,
          fontWeight: 'normal',
          textAlign: 'center',
        }}
      >
        You found treasure!
      </h1>
      <Icon size={200} src={ChestIcon} style={{ marginBottom: 24 }} />
      <Button onClick={() => openCurrent()}>Open</Button>
    </FlexContainer>
  )
}
